body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

img {
  width: 100%;
}
.box{
  width: 100%;
  padding: 20px 10px 20px 10px;
  background-color: #eeeeee;
  border-radius: 10px;
}
.bg-gradient{
  background-image: -webkit-gradient(linear , left top, right top , from(#16222A) , to(#3A6073));
  background-image: linear-gradient(to right , #16222A , #3A6073);
  font-size: 15px;
}
.min-h{

  minimum-height : 72vh;
}

.h-72{
  height: 72vh;
}

.landing {
  position: relative;
  background: url('https://www.bestconsent.com/wp-content/uploads/2016/05/best-consent-login-background.jpg') no-repeat;
  background-size: cover;
  background-position: center;
  height: 100vh;
  margin-top: -24px;
  margin-bottom: -50px;
}

.landing-inner {
  padding-top: 80px;
}

.dark-overlay {
  background-color: rgba(0, 0, 0, 0.7);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.card-form {
  opacity: 0.9;
}

.latest-profiles-img {
  width: 40px;
  height: 40px;
}

.form-control::-webkit-input-placeholder {
  color: #bbb !important;
}

.form-control::-ms-input-placeholder {
  color: #bbb !important;
}

.form-control::placeholder {
  color: #bbb !important;
}

.load-box{
    width: 50%;
}
.boxline{
    height: 12px;
    margin: 10px;
    -webkit-animation: pulse 1s infinite ease-in-out ;
            animation: pulse 1s infinite ease-in-out ;
}
.load-box div:nth-child(1){
    width: 300px;
}
.load-box div:nth-child(2){
    width: 180px;
}
.load-box div:nth-child(3){
    width: 290px;
}
.load-box div:nth-child(4){
    width: 400px;
}
.load-box div:nth-child(5){
    width: 50px;
}

@-webkit-keyframes pulse{
    0%{
        background-color: rgba(165,165,165,.1);
    }
    25%{
        background-color: rgba(165,165,165,.2);
    }
    50%{
        background-color: rgba(165,165,165,.3);
    }
    75%{
    background-color: rgba(165,165,165,.3);
    }
    100%{
    background-color: rgba(165,165,165,.1);
    }
    }

@keyframes pulse{
    0%{
        background-color: rgba(165,165,165,.1);
    }
    25%{
        background-color: rgba(165,165,165,.2);
    }
    50%{
        background-color: rgba(165,165,165,.3);
    }
    75%{
    background-color: rgba(165,165,165,.3);
    }
    100%{
    background-color: rgba(165,165,165,.1);
    }
    }

